    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.main-container{
    height: 100%;
    width: 100%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    gap: 10px;
    
}

.side{
    align-self: flex-start;
    height: 100%;
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.view-btns{
    height: 30%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.heading{
    color: #000;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.display{
    height: 100%;
    width: 80%;
    background-color: rgb(223, 226, 223);
    border-radius: 20px;
    overflow: auto;
    padding: 20px;
}

.task-btn, .display-add-btn, .add-task, .add-project{
    border: transparent;
    background-color: transparent;
    cursor: pointer;
    font-weight: 700;
    font-size: 1rem;
}

.task-btn{
    color: #000;
    height: 2.5rem;
    width: 100%;
    border-radius: 10px;
    background: transparent;
    transition: all 0.5s;
}

.task-btn:hover{
    background-color: #e7e4e4;
}

.projects-container{
    width: 100%;
    height: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.project-heading{
    align-self: flex-start;
    color: #000;
}

.project-list{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    overflow: auto;
    border-radius: 10px;
}

.project-btn{
    border: transparent;
    background: transparent;
    font-weight: 700;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.4s;
    height: 2.5rem;
    width: 100%;
    border-radius: 10px;
    transition: all 0.5s;
    color: #000;
}

.project-btn:hover{
    background-color: #e7e4e4;
}

.project-item{
    list-style: none;
    width: 100%;
}

.project-display{
    height: 100%;
    width: 100%;
}

.project{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.project-header{
    width: 100%;
    height: 20%;
    display: flex;
    flex-direction: column-reverse;
    gap: 5px;
    /* align-items: center; */
}

.project-header .delete{
    align-self: flex-end;
    height: 30%;
    width: 10%;
    cursor: pointer;
}

.add-project-task{
    align-self: flex-end;
    background: rgb(61, 177, 61);
    height: 30%;
    width: 10%;
    border: transparent;
    border-radius: 10px;
    color: #fff;
    font-weight: 700;
    cursor: pointer;
}

.modal-btns{
    z-index: 999;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.display-add-btn{
    background-color: #fff;
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
    font-size: 3rem;
    transition: all 0.5s;
    transition: all 0.5s;
}

.add-task, .add-project{
    background: #292727;
    transform: translateY(10px);
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s;
    height: 50%;
    width: 36%;
    border-radius: 10px;
    color: #fff;
    font-weight: 700;
    font-size: 0.9rem;
}

.show{
    visibility: visible;
    opacity: 1;
    transform: translateY(-10px);
}

.preview{
    width: 100%;
    height: 10%;
    border-radius: 10px;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgb(255, 255, 255);
    margin-bottom: 5px;
    transition: all 0.5s;
    position: relative;
}

.task{
    height: 50%;
    width: 50%;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    padding: 10px;
    border-radius: 10px;
    font-weight: 700;
    transition: all 0.5s;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
}

.close-task{
    height: 20px;
    width: 40px;
    background-color: transparent;
    border-radius: 10px;
    border: transparent;
    font-weight: 700;
    outline: none;
    cursor: pointer;
    align-self: flex-end;
}

.info{
    height: 100%;
    width: 50%;
    display: flex;
    align-items: center;
    gap: 10px;
}

.priority{
    width: 10px;
    height: 100%;
    background: lightgreen;
    border-radius: 10px 0 10px 0;
}

.checkBox{
    margin-left: 10px;
    margin-right: 10px;
    height: 20px;
    width: 20px;
    border: 3px solid lightgreen;
    cursor: pointer;
}

.info-btns{
    height: 100%;
    width: 50%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 30px;
    margin-right: 10px;
}

.viewBtn, .viewBtnPr, .delete, .deletePr{
    width: 20%;
    height: 50%;
    border-radius: 10px;
    border: transparent;
    font-weight: 700;
    
    color: #fff;
    cursor: pointer;
}

.viewBtn, .viewBtnPr{
    background-color: burlywood;
}

.delete, .deletePr{
    background-color: rgb(238, 77, 77);
}

.view{
    position: absolute;
    transition: transform 0.5s;
    width: 100%;
    height: 100%;
    background-color: red;
    z-index: -1;
}

.overlay{
    height: 100%;
    width: 100%;
    position: absolute;
    backdrop-filter: blur(3px);
    transition: all 0.3s;
    z-index: 10;
}

.modal-task, .modal-project-task{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 90%;
    width: 50%;
    transition: all 0.3s;
    border-radius: 20px;
    background: #3d3c3c;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 10px;
    padding: 20px;
    color: #ffffff;
    font-weight: 700;
    
    z-index: 999;
}

.task-form{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}


.modal-task-heading{
    margin-bottom: 20px;
}

.label{
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.title-task, .date-task, .title-Ptask, .date-Ptask{
    font-size: 1rem;
    width: 50%;
    padding: 5px;
    border-radius: 10px;
    outline: none;
    border: transparent;
}

.title-task, .titel-Ptask{
    height: 7%;
}

.title-task::placeholder, .title-Ptask::placeholder{
    font-size: 1rem;
}

.description-task, .description-Ptask{
    width: 80%;
    height: 7rem;
    padding: 5px;
    border-radius: 10px;
    outline: none;
    border: transparent;
    overflow: auto;
    resize: none;
}

.modal-task-add-btn, .modal-project-task-add-btn, .modal-project-add-btn{
    color: black;
    background-color: #fff;
    height: 10%;
    width: 20%;
    display: grid;
    place-items: center;
    border-radius: 10px;
    align-self: flex-end;
    cursor: pointer;
    font-weight: 700;
    
    font-size: 1rem;
    border: transparent;
}

.high, .moderate, .low{
    width: 60%;
    height: 70%;
    padding: 10px;
    border-radius: 5px;
    border: transparent;
    color: #fff;
    font-size: 1rem;
    font-weight: 700;
    
    cursor: pointer;
    opacity: 0.5;
    transition: opacity 0.5s;
}

.high:hover, .moderate:hover, .low:hover{
    opacity: 1;
}

.highPr,.high{
    background: rgb(238, 77, 77);
}

.moderatePr,.moderate{
    background: rgb(47, 107, 238);
}

.lowPr,.low{
    background: rgb(53, 201, 53);
}

.red{
    color: rgb(238, 77, 77);
}

.blue{
    color: rgb(47, 107, 238); 
}

.green{
    color: rgb(53, 201, 53);
}

.modal-project{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 50%;
    width: 40%;
    transition: all 0.3s;
    border-radius: 20px;
    background: #3d3c3c;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 20px;
    color: #ffffff;
    font-weight: 700;
    
    z-index: 999;
}

.title-project{
    width: 70%;
    height: 20%;
    padding: 10px;
    font-size: 1rem;
    border-radius: 10px;
    border: transparent;
    outline: none;
}

.modal-project-add-btn{
    align-self: flex-start;
    font-weight: 700;
    padding: 5px;
    height: 15%;
}

.hidden{
    height: 0;
    width: 0;
    left: 0;
    bottom: 0;
    opacity: 0;
    visibility: hidden;
}

.highlight{
    opacity: 1;
}
