 width: 100%;\n    height: 60%;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 10px;\n}\n\n.project-heading{\n    align-self: flex-start;\n    color: #000;\n}\n\n.project-list{\n    width: 100%;\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 10px;\n    overflow: auto;\n    border-radius: 10px;\n}\n\n.project-btn{\n    border: transparent;\n    background: transparent;\n    font-weight: 700;\n    font-size: 1rem;\n    cursor: pointer;\n    transition: all 0.4s;\n    height: 2.5rem;\n    width: 100%;\n    border-radius: 10px;\n    transition: all 0.5s;\n    color: #000;\n}\n\n.project-btn:hover{\n    background-color: #e7e4e4;\n}\n\n.project-item{\n    list-style: none;\n    width: 100%;\n}\n\n.project-display{\n    height: 100%;\n    width: 100%;\n}\n\n.project{\n    height: 100%;\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n.project-header{\n    width: 100%;\n    height: 20%;\n    display: flex;\n    flex-direction: column-reverse;\n    gap: 5px;\n    /* align-items: center; */\n}\n\n.project-header .delete{\n    align-self: flex-end;\n    height: 30%;\n    width: 10%;\n    cursor: pointer;\n}\n\n.add-project-task{\n    align-self: flex-end;\n    background: rgb(61, 177, 61);\n    height: 30%;\n    width: 10%;\n    border: transparent;\n    border-radius: 10px;\n    color: #fff;\n    font-weight: 700;\n    cursor: pointer;\n}\n\n.modal-btns{\n    z-index: 999;\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n}\n\n.display-add-btn{\n    background-color: #fff;\n    height: 4rem;\n    width: 4rem;\n    border-radius: 50%;\n    font-size: 3rem;\n    transition: all 0.5s;\n    transition: all 0.5s;\n}\n\n.add-task, .add-project{\n    background: #292727;\n    transform: translateY(10px);\n    opacity: 0;\n    visibility: hidden;\n    transition: all 0.5s;\n    height: 50%;\n    width: 36%;\n    border-radius: 10px;\n    color: #fff;\n    font-weight: 700;\n    font-size: 0.9rem;\n}\n\n.show{\n    visibility: visible;\n    opacity: 1;\n    transform: translateY(-10px);\n}\n\n.preview{\n    width: 100%;\n    height: 10%;\n    border-radius: 10px;\n    position: relative;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    background: rgb(255, 255, 255);\n    margin-bottom: 5px;\n    transition: all 0.5s;\n    position: relative;\n}\n\n.task{\n    height: 50%;\n    width: 50%;\n    background-color: rgb(255, 255, 255);\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n    align-items: flex-start;\n    padding: 10px;\n    border-radius: 10px;\n    font-weight: 700;\n    transition: all 0.5s;\n    position: absolute;\n    left: 50%;\n    top: 50%;\n    transform: translate(-50%, -50%);\n    z-index: 999;\n}\n\n.close-task{\n    height: 20px;\n    width: 40px;\n    background-color: transparent;\n    border-radius: 10px;\n    border: transparent;\n    font-weight: 700;\n    outline: none;\n    cursor: pointer;\n    align-self: flex-end;\n}\n\n.info{\n    height: 100%;\n    width: 50%;\n    display: flex;\n    align-items: center;\n    gap: 10px;\n}\n\n.priority{\n    width: 10px;\n    height: 100%;\n    background: lightgreen;\n    border-radius: 10px 0 10px 0;\n}\n\n.checkBox{\n    margin-left: 10px;\n    margin-right: 10px;\n    height: 20px;\n    width: 20px;\n    border: 3px solid lightgreen;\n    cursor: pointer;\n}\n\n.info-btns{\n    height: 100%;\n    width: 50%;\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: 30px;\n    margin-right: 10px;\n}\n\n.viewBtn, .viewBtnPr, .delete, .deletePr{\n    width: 20%;\n    height: 50%;\n    border-radius: 10px;\n    border: transparent;\n    font-weight: 700;\n    \n    color: #fff;\n    cursor: pointer;\n}\n\n.viewBtn, .viewBtnPr{\n    background-color: burlywood;\n}\n\n.delete, .deletePr{\n    background-color: rgb(238, 77, 77);\n}\n\n.view{\n    position: absolute;\n    transition: transform 0.5s;\n    width: 100%;\n    height: 100%;\n    background-color: red;\n    z-index: -1;\n}\n\n.overlay{\n    height: 100%;\n    width: 100%;\n    position: absolute;\n    backdrop-filter: blur(3px);\n    transition: all 0.3s;\n    z-index: 10;\n}\n\n.modal-task, .modal-project-task{\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%,-50%);\n    height: 90%;\n    width: 50%;\n    transition: all 0.3s;\n    border-radius: 20px;\n    background: #3d3c3c;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-evenly;\n    gap: 10px;\n    padding: 20px;\n    color: #ffffff;\n    font-weight: 700;\n    \n    z-index: 999;\n}\n\n.task-form{\n    height: 100%;\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n}\n\n\n.modal-task-heading{\n    margin-bottom: 20px;\n}\n\n.label{\n    display: flex;\n    flex-direction: column;\n    gap: 5px;\n}\n\n.title-task, .date-task, .title-Ptask, .date-Ptask{\n    font-size: 1rem;\n    width: 50%;\n    padding: 5px;\n    border-radius: 10px;\n    outline: none;\n    border: transparent;\n}\n\n.title-task, .titel-Ptask{\n    height: 7%;\n}\n\n.title-task::placeholder, .title-Ptask::placeholder{\n    font-size: 1rem;\n}\n\n.description-task, .description-Ptask{\n    width: 80%;\n    height: 7rem;\n    padding: 5px;\n    border-radius: 10px;\n    outline: none;\n    border: transparent;\n    overflow: auto;\n    resize: none;\n}\n\n.modal-task-add-btn, .modal-project-task-add-btn, .modal-project-add-btn{\n    color: black;\n    background-color: #fff;\n    height: 10%;\n    width: 20%;\n    display: grid;\n    place-items: center;\n    border-radius: 10px;\n    align-self: flex-end;\n    cursor: pointer;\n    font-weight: 700;\n    \n    font-size: 1rem;\n    border: transparent;\n}\n\n.high, .moderate, .low{\n    width: 60%;\n    height: 70%;\n  